$bar-width: 32px;
$bar-height: 2px;
$bar-spacing: 6px;
$bar-border-radius: 0;
$bar-color: $dark;
$opening-time: 600ms;

.hamburger-menu {
  position: relative;
  display: inline-block;
  width: $bar-width;
  height: $bar-width;
  padding: 0;
  text-decoration: none;
  background-color: transparent;
  border: none;

  &.active {
    .hamburger-menu__bars {
      background-color: rgba($bar-color, 0);

      &:before {
        bottom: 0;
        transform: rotate(-45deg);
        visibility: visible;
        transition:
          bottom $opening-time * 0.5 cubic-bezier(0.23, 1, 0.32, 1),
          transform $opening-time * 0.5 $opening-time * 0.5 cubic-bezier(0.23, 1, 0.32, 1);
      }

      &:after {
        top: 0;
        transform: rotate(45deg);
        visibility: visible;
        transition:
          top $opening-time * 0.5 cubic-bezier(0.23, 1, 0.32, 1),
          transform $opening-time * 0.5 $opening-time * 0.5 cubic-bezier(0.23, 1, 0.32, 1);
      }
    }
  }

  &.without-animation {
    .hamburger-menu__bars,
    .hamburger-menu__bars:before,
    .hamburger-menu__bars:after {
      transition: none;
    }
  }

  &__wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    width: $bar-width;
    height: $bar-height * 3 + $bar-spacing * 2;
    transform: translate(-50%, -50%);
  }

  &__bars,
  &__bars:before,
  &__bars:after {
    width: $bar-width;
    height: $bar-height;
    background-color: $bar-color;
    border-radius: $bar-border-radius;
  }

  &__bars {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: background-color 0ms $opening-time * 0.5;

    &:before {
      position: absolute;
      bottom: $bar-spacing + $bar-height;
      left: 0;
      visibility: visible;
      transition:
        bottom $opening-time * 0.5 $opening-time * 0.5 cubic-bezier(0.23, 1, 0.32, 1),
        transform $opening-time * 0.5 cubic-bezier(0.23, 1, 0.32, 1);
      content: "";
    }

    &:after {
      position: absolute;
      top: $bar-spacing + $bar-height;
      left: 0;
      visibility: visible;
      transition:
        top $opening-time * 0.5 $opening-time * 0.5 cubic-bezier(0.23, 1, 0.32, 1),
        transform $opening-time * 0.5 cubic-bezier(0.23, 1, 0.32, 1);
      content: "";
    }
  }
}
