.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: $zindex-fixed;
  display: flex;
  align-items: center;
  min-height: $header-height;
  padding: 0.5rem 0;
  transition:
    background-color 0.3s,
    box-shadow 0.3s;

  @include media-breakpoint-up(lg) {
    left: $sidebar-width;
  }

  &.active {
    background-color: $gray-200;
    box-shadow: $box-shadow;
  }

  &__logo {
    position: relative;
    display: inline-flex;
    width: 100%;
    max-width: 120px;
  }
}
