.sidebar {
  padding: $offcanvas-padding-y $offcanvas-padding-x;

  @include media-breakpoint-up(lg) {
    position: fixed;
    top: 0;
    left: 0;
    z-index: $zindex-offcanvas;
    display: flex;
    flex-direction: column;
    width: $sidebar-width;
    height: 100dvh;
    background-color: $offcanvas-bg-color !important;
    box-shadow: $offcanvas-box-shadow;
  }

  &__header {
    padding-bottom: rem(16px);
    padding-left: rem(24px);
  }

  &__logo {
    position: relative;
    display: inline-flex;
    width: 100%;
    max-width: 120px;
  }

  &__body {
    position: relative;
    flex-grow: 1;
    overflow: hidden;

    &:before,
    &:after {
      position: absolute;
      right: 0;
      left: 0;
      z-index: 5;
      width: 100%;
      height: rem(28px);
      background-image: linear-gradient(to bottom, $white, transparent);
      content: "";
      pointer-events: none;
    }

    &:before {
      top: 0;
    }

    &:after {
      bottom: 0;
      transform: rotate(180deg);
    }
  }

  &__footer {
    padding-top: rem(24px);
  }

  &__nav {
    @extend %custom-scrollbar;

    height: 100%;
    padding: rem(28px) 0;
    overflow-y: auto;
  }

  &__nav-list {
    @include list-reset;
  }

  &__nav-item {
    &:not(:last-child) {
      margin-bottom: rem(24px);
    }
  }
}
