.form-switch {
  margin-bottom: 0;

  .form-check-input {
    height: rem(16px);
    background-color: $gray-dark;
    border: none;
    cursor: pointer;
    filter: none !important;

    &:checked {
      background-color: $primary;
    }

    &:disabled {
      background-color: $input-disabled-bg;
      cursor: not-allowed;
      opacity: 1;
      pointer-events: auto;
    }
  }

  .form-check-label {
    padding-left: rem(12px);
    cursor: pointer;
  }
}
