.list {
  &-with-check {
    @include list-reset;

    li {
      position: relative;
      padding-left: rem(32px);

      &:not(:last-child) {
        margin-bottom: rem(24px);
      }

      &:before {
        @include size(rem(24px));
        @include inline-flex-center;

        position: absolute;
        top: rem(-2px);
        left: 0;
        z-index: 5;
        color: $dark;
        font-size: rem(14px);
        font-family: "icomoon";
        background-color: $primary;
        border-radius: $border-radius-pill;
        content: "\e91a";
      }
    }
  }
}
