@if $use-title-palette {
  @include title-sizes;
}

.text-block,
.remove-last-child-margin {
  > * {
    &:last-child {
      margin-bottom: 0 !important;
    }
  }

  &.row {
    > * {
      &.col,
      &[class^="col-"] {
        &:last-child {
          > * {
            margin-bottom: 0 !important;
          }
        }
      }
    }
  }
}

.text-sm {
  font-size: $font-size-sm !important;
  line-height: $line-height-sm !important;
}

.text-lg {
  font-size: $font-size-lg !important;
  line-height: $line-height-lg !important;
}

.title-with-icon {
  &__icon {
    @include size(rem(24px));
    @include inline-flex-center;

    margin-right: 0.08333333em;
    color: $dark;
    font-size: rem(24px);
    vertical-align: middle;
    background-color: $primary;
    border-radius: $border-radius-xs;
  }

  &__text {
    vertical-align: middle;
  }
}

.text-alert {
  position: relative;
  padding-left: rem(24px);
  font-weight: $font-weight-normal;
  font-size: $font-size-sm;
  line-height: $line-height-lg;

  &:before {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    font-size: rem(16px);
    font-family: "icomoon";
    line-height: 1;
    content: "\e917";
  }

  > * {
    &:last-child {
      margin-bottom: 0 !important;
    }
  }
}

.text-with-bg-star {
  position: relative;
  z-index: 1;
  display: inline-block;
  padding: 0 0.61538461em;

  &:before {
    @include inline-flex-center;

    position: absolute;
    top: 50%;
    left: 0;
    z-index: -1;
    width: 100%;
    background-image: url("../img/text-with-bg-star-img.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    transform: translateY(-50%);
    content: "";
    aspect-ratio: 1 / 1;
  }
}
