.pagination {
  gap: rem(12px);
  margin-bottom: 0;
  font-weight: $font-weight-normal;

  .page-item {
    &.disabled {
      cursor: not-allowed;
    }
  }

  .page-link {
    @include size(rem(28px));
    @include inline-flex-center;

    &.disabled {
      cursor: not-allowed;
    }

    &[aria-label] {
      font-size: rem(20px);
    }

    &[aria-label="Previous"] {
      @include media-breakpoint-up(sm) {
        margin-right: rem(12px);
      }
    }

    &[aria-label="Next"] {
      @include media-breakpoint-up(sm) {
        margin-left: rem(12px);
      }
    }
  }
}
