.card {
  &-default {
    width: 100%;
    padding: rem(24px 16px);
    background-color: $white;
    border-radius: $border-radius;
    box-shadow: $box-shadow;

    @include media-breakpoint-up(sm) {
      padding: rem(24px);
      border-radius: $border-radius-xl;
    }

    &.centered {
      max-width: 1152px;
      margin: 0 auto;
    }

    &__header {
      display: flex;
      align-items: center;
      min-height: rem(40px);
      margin-bottom: rem(24px);
    }
  }

  &-animal {
    position: relative;
    width: 100%;
    padding: rem(24px);
    background-color: $white;
    border: $border-width $border-style $gray-100;
    border-radius: $border-radius-xl;
    box-shadow: $box-shadow;

    &__photo {
      margin-bottom: rem(24px);
      text-align: center;
    }

    &__info {
      max-width: 324px;
      margin: 0 auto rem(32px);
    }

    &__delete-btn {
      position: absolute;
      top: rem(20px);
      right: rem(20px);
      z-index: 5;
    }
  }

  &-document {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: rem(16px);
    background-color: $white;
    border-radius: $border-radius;
    box-shadow: $box-shadow;

    @include media-breakpoint-up(sm) {
      padding: rem(24px);
      border-radius: $border-radius-xl;
    }

    &:hover {
      .card-document__actions {
        visibility: visible;
        opacity: 1;
      }
    }

    &__content-top {
      flex-grow: 1;
    }

    &__document-img {
      @include responsive-ratio(296, 387);

      position: relative;
      margin-bottom: rem(24px);
      overflow: hidden;
    }

    &__actions {
      position: absolute;
      top: rem(20px);
      right: rem(20px);
      z-index: 5;
      padding: rem(12px);
      background-color: $white;
      border-radius: $border-radius-sm;
      box-shadow: $box-shadow;
      visibility: hidden;
      opacity: 0;
      transition:
        opacity 0.3s ease,
        visibility 0.3s ease;
    }
  }
}
