label {
  &.form-label {
    cursor: pointer;
  }
}

.form-control {
  caret-color: $input-color;
  text-overflow: ellipsis;

  &.form-control-sm {
    line-height: 1.14285714;
  }

  &:not([disabled]) {
    &:hover {
      background-color: $yellow-light;
    }
  }

  &[disabled] {
    -webkit-text-fill-color: $input-disabled-color;
    cursor: not-allowed;
  }
}

textarea {
  &.form-control {
    height: 140px;
  }
}

.form-select {
  &:not([disabled]) {
    cursor: pointer;

    &:hover {
      background-color: $yellow-light;
    }
  }

  &[disabled] {
    cursor: not-allowed;
  }

  &[multiple] {
    height: $input-height;
  }

  &.form-select-sm {
    padding-right: rem(26px);
    line-height: 1.14285714;
    background-position: right $form-select-padding-x-sm center;
    background-size: rem(12px);

    &.filter-select {
      padding-left: rem(26px);
      background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M7.52174 9.81818H11M7.52174 11.6364H10.1304M1 3H9.69565V4.81818L6.53391 8.90909V13L4.16174 12.0909V8.90909L1 4.81818V3Z' stroke='#{$form-select-indicator-color}' stroke-width='0.8' stroke-linejoin='round'/></svg>")), escape-svg($form-select-indicator);
      background-repeat: no-repeat;
      background-position:
        left $form-select-padding-x-sm center,
        right $form-select-padding-x-sm center;
      background-size: rem(16px), rem(12px);
      border-color: $dark;
    }
  }
}

.position-relative {
  > .invalid-feedback {
    position: absolute;
    top: calc(100% + $form-feedback-margin-top);
    left: 0;
    z-index: 5;
    margin-top: 0;
  }
}
