.of-contain {
  @include absolute-cover;

  object-fit: contain;
}

.of-cover {
  @include absolute-cover;

  object-fit: cover;
}

%custom-scrollbar {
  @include custom-scrollbar(8px, $yellow-light, $dark, 8px, 2px);
}

.custom-scrollbar {
  @extend %custom-scrollbar;
}

%custom-scrollbar-sm {
  @include custom-scrollbar(4px, $yellow-light, $dark, 4px, 1px);
}

.custom-scrollbar-sm {
  @extend %custom-scrollbar-sm;
}

.tabular-nums {
  font-variant-numeric: tabular-nums;
}

.resize-animation-stopper * {
  transition: none !important;
}

.bordered-cols {
  & > [class*="col"] {
    &:not(.col-12) {
      border-right: $border-width $border-style $border-color;
    }

    &:last-child {
      border-right: none;
    }

    &.col-xs-6 {
      @include media-breakpoint-down(xs) {
        border-right: none;
      }
    }
  }
}

.text-decoration-underline {
  text-decoration-thickness: 0.5px !important;
  text-underline-offset: 2px;
}
