.photo-item {
  @include size(rem(100px));

  position: relative;
  display: inline-flex;
  background-color: $gray-200;
  border: 2px solid $primary;
  border-radius: $border-radius-pill;

  &.sm {
    @include size(rem(80px));

    .photo-item__uploader {
      right: 0;
      bottom: 0;
    }
  }

  &__uploader-container {
    @include absolute-cover;

    z-index: 5;
    visibility: hidden;
    opacity: 0;
    transition:
      opacity 0.3s ease-in-out,
      visibility 0.3s ease-in-out;

    &.active {
      visibility: visible;
      opacity: 1;
    }
  }

  &__uploader {
    position: absolute;
    right: rem(2px);
    bottom: rem(2px);
    z-index: 10;
    display: inline-flex;
  }

  &__uploader-input {
    @include absolute-cover;

    z-index: 5;
    font-size: 0;
    background-color: transparent;
    border: none;
    cursor: pointer;
    opacity: 0;
  }

  &__delete-overlay {
    @include absolute-cover;
    @include flex-center;

    z-index: 5;
    background-color: rgba($black, 0.5);
    border-radius: $border-radius-pill;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;

    &:hover {
      opacity: 1;
    }
  }
}
