.btn {
  @include inline-flex-center;

  // sizes
  &:not(.btn-sm, .btn-lg) {
    gap: 0.75em;
    min-width: 13.5em;
    min-height: 2.5em;
    font-weight: $font-weight-medium;
    font-family: $font-family-sans-serif-2;

    [class^="icomoon-"] {
      font-size: 1.5em;
    }
  }

  &-sm {
    gap: 0.28571428em;
    min-height: 2.42857142em;

    [class^="icomoon-"] {
      font-size: 1.14285714em;
    }
  }

  // colors
  &-primary {
    @include button-variant($primary, $primary, $dark, $dark, $dark, $white, lighten($dark, 10%), lighten($dark, 10%), $white);
  }

  &-outline-dark {
    @include button-variant(transparent, $dark, $black, $gray, $gray, $dark, lighten($gray, 10%), lighten($gray, 10%), $dark);
  }

  &-gray-light {
    @include button-variant($gray-light, $gray-light, $black, $danger, $danger, $black, darken($danger, 10%), darken($danger, 10%), $black);
  }

  &-link-gray-dark {
    @include button-variant(transparent, transparent, $gray-dark, $primary, $primary, $dark, lighten($primary, 10%), lighten($primary, 10%), $dark);
  }

  // types
  &-menu {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    min-height: 2.5em;
    padding: 0.25em 1.5em 0.25em 3.75em;
    color: $dark;
    font-weight: $font-weight-medium;
    font-size: 1rem;
    font-family: $font-family-sans-serif-2;
    text-decoration: none;
    border-radius: $border-radius-pill;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: $yellow-light;
    }

    &.active {
      background-color: $primary;
    }

    &__icon {
      @include size(1em);
      @include inline-flex-center;

      position: absolute;
      left: 1em;
      font-size: 1.5em;
      background-color: $primary;
      border-radius: $border-radius-xs;
    }
  }

  &-action {
    min-width: auto !important;
    min-height: auto !important;
    padding: rem(4px);
    border-radius: $border-radius-pill;

    &:not(.btn-sm, .btn-lg) {
      @include size(rem(40px));

      font-size: rem(12px);
    }

    &.btn-sm {
      @include size(rem(24px));

      font-size: rem(12px);

      .icomoon-arrow-chevron-left,
      .icomoon-arrow-chevron-right,
      .icomoon-arrow-chevron-top,
      .icomoon-arrow-chevron-bottom {
        font-size: rem(18px);
      }
    }
  }

  &-close {
    box-sizing: border-box;
    padding: $btn-close-padding-x !important;
    transition:
      transform 0.3s ease,
      opacity 0.3s ease;

    &:hover {
      transform: rotate(90deg);
    }
  }
}
