.form-control {
  &.datepicker {
    padding-right: $form-select-indicator-padding;
    background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M10 10H14.1667V14.1667H10V10ZM15.8333 2.50004H15V0.833374H13.3333V2.50004H6.66667V0.833374H5V2.50004H4.16667C3.25 2.50004 2.5 3.25004 2.5 4.16671V15.8334C2.5 16.75 3.25 17.5 4.16667 17.5H15.8333C16.75 17.5 17.5 16.75 17.5 15.8334V4.16671C17.5 3.25004 16.75 2.50004 15.8333 2.50004ZM15.8333 4.16671V5.83337H4.16667V4.16671H15.8333ZM4.16667 15.8334V7.50004H15.8333V15.8334H4.16667Z' fill='#{$form-select-indicator-color}'/></svg>"));
    background-repeat: no-repeat;
    background-position: right $input-padding-x center;
    background-size: 1.25rem;

    &:focus {
      background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M10 10H14.1667V14.1667H10V10ZM15.8333 2.50004H15V0.833374H13.3333V2.50004H6.66667V0.833374H5V2.50004H4.16667C3.25 2.50004 2.5 3.25004 2.5 4.16671V15.8334C2.5 16.75 3.25 17.5 4.16667 17.5H15.8333C16.75 17.5 17.5 16.75 17.5 15.8334V4.16671C17.5 3.25004 16.75 2.50004 15.8333 2.50004ZM15.8333 4.16671V5.83337H4.16667V4.16671H15.8333ZM4.16667 15.8334V7.50004H15.8333V15.8334H4.16667Z' fill='#{$input-focus-border-color}'/></svg>"));
    }
  }
}

.air-datepicker {
  --adp-font-family: #{$font-family-sans-serif-2};
  --adp-font-size: #{$font-size-sm};
  --adp-width: 308px;
  --adp-z-index: #{$zindex-dropdown};
  --adp-padding: 1.5rem;
  --adp-grid-areas: "nav" "body" "timepicker" "buttons";
  --adp-transition-duration: 0.3s;
  --adp-transition-ease: ease-out;
  --adp-transition-offset: 0.5rem;
  --adp-background-color: #{$white};
  --adp-background-color-hover: #{$yellow-light};
  --adp-background-color-active: #{$primary};
  --adp-background-color-in-range: rgb(92 196 239 / 0.1);
  --adp-background-color-in-range-focused: rgb(92 196 239 / 0.2);
  --adp-background-color-selected-other-month-focused: #{$primary};
  --adp-background-color-selected-other-month: #{$primary};
  --adp-color: #{$dark};
  --adp-color-secondary: #{$dark};
  --adp-accent-color: #{$primary};
  --adp-color-current-date: var(--adp-accent-color);
  --adp-color-other-month: #{$gray};
  --adp-color-disabled: #{$gray};
  --adp-color-disabled-in-range: #939393;
  --adp-color-other-month-hover: #c5c5c5;
  --adp-border-color: #{$dark};
  --adp-border-color-inner: var(--adp-border-color);
  --adp-border-radius: #{$border-radius-xs};
  --adp-border-color-inline: #d7d7d7;
  --adp-nav-height: auto;
  --adp-nav-arrow-color: var(--adp-color-secondary);
  --adp-nav-action-size: 1.5rem;
  --adp-nav-color-secondary: var(--adp-color-secondary);
  --adp-day-name-color: #{$gray};
  --adp-day-name-color-hover: #{$primary};
  --adp-day-cell-width: 1fr;
  --adp-day-cell-height: 1.5rem;
  --adp-month-cell-height: 42px;
  --adp-year-cell-height: 56px;
  --adp-pointer-size: 10px;
  --adp-poiner-border-radius: 2px;
  --adp-pointer-offset: 14px;
  --adp-cell-border-radius: #{$border-radius-pill};
  --adp-cell-background-color-hover: var(--adp-background-color-hover);
  --adp-cell-background-color-selected: #{$primary};
  --adp-cell-background-color-selected-hover: #{$primary};
  --adp-cell-background-color-in-range: #{$primary};
  --adp-cell-background-color-in-range-hover: #{$primary};
  --adp-cell-border-color-in-range: var(--adp-cell-background-color-selected);
  --adp-btn-height: 32px;
  --adp-btn-color: var(--adp-accent-color);
  --adp-btn-color-hover: var(--adp-color);
  --adp-btn-border-radius: var(--adp-border-radius);
  --adp-btn-background-color-hover: var(--adp-background-color-hover);
  --adp-btn-background-color-active: var(--adp-background-color-active);
  --adp-time-track-height: 1px;
  --adp-time-track-color: #dedede;
  --adp-time-track-color-hover: #b1b1b1;
  --adp-time-thumb-size: 12px;
  --adp-time-padding-inner: 10px;
  --adp-time-day-period-color: var(--adp-color-secondary);
  --adp-mobile-font-size: 16px;
  --adp-mobile-nav-height: 40px;
  --adp-mobile-width: 320px;
  --adp-mobile-day-cell-height: 38px;
  --adp-mobile-month-cell-height: 48px;
  --adp-mobile-year-cell-height: 64px;

  font-weight: $font-weight-medium;
  box-shadow: none;

  &--pointer {
    display: none;
  }

  &--navigation {
    padding: var(--adp-padding) var(--adp-padding) 0;
  }

  &-nav {
    padding: 0 0 0.75rem;

    &--action {
      height: var(--adp-nav-action-size);
      border-radius: $border-radius-pill;
      transition:
        background-color 0.3s ease-out,
        color 0.3s ease-out;

      path {
        fill: var(--adp-nav-arrow-color);
        stroke: none;
      }
    }

    &--title {
      font-weight: $font-weight-semibold;
      transition:
        background-color 0.3s ease-out,
        color 0.3s ease-out;
    }
  }

  &--content {
    padding-top: 1.25rem;
  }

  &-body {
    /* stylelint-disable selector-class-pattern */
    &--day-names {
      margin-top: 0;
      margin-bottom: 1.25rem;
    }

    &--day-name {
      font-weight: $font-weight-semibold;
      font-size: 0.625rem;
    }

    &--cells {
      &.-days- {
        gap: 1rem;
      }
    }

    /* stylelint-enable selector-class-pattern */
  }

  &-cell {
    transition:
      background-color 0.3s ease-out,
      color 0.3s ease-out;

    /* stylelint-disable selector-class-pattern */
    &.-selected- {
      color: var(--adp-color) !important;
    }

    &.-current- {
      &.-focus- {
        color: var(--adp-color-current-date);
      }

      &.-in-range- {
        color: var(--adp-color);
      }
    }

    &.-disabled- {
      color: var(--adp-color-disabled) !important;
      background-color: transparent !important;
      cursor: not-allowed !important;
    }

    /* stylelint-enable selector-class-pattern */
  }
}

.air-datepicker-overlay {
  --adp-overlay-background-color: #{rgba($modal-backdrop-bg, $modal-backdrop-opacity)};
  --adp-overlay-transition-duration: 0.3s;
  --adp-overlay-transition-ease: ease-out;
  --adp-overlay-z-index: #{$zindex-modal-backdrop};
}
