.select2-container {
  &--bootstrap {
    display: block;
    width: auto !important;

    &.select2-container--open {
      .select2-selection {
        &__arrow {
          background-image: escape-svg($form-select-focus-indicator);
          transform: translate(0, -50%) rotate(180deg);
        }

        &--multiple {
          &:after {
            background-image: escape-svg($form-select-focus-indicator);
            transform: translate(0, -50%) rotate(180deg);
          }
        }
      }

      &.select2-container--open {
        .select2-selection {
          background-color: $input-focus-bg;
          border-color: $form-select-focus-border-color;
          box-shadow: $form-select-focus-box-shadow;
        }
      }
    }

    .select2-selection {
      height: auto;
      padding: $form-select-padding-y $form-select-indicator-padding $form-select-padding-y $form-select-padding-x;
      color: $form-select-color;
      font-weight: $form-select-font-weight;
      font-size: $form-select-font-size;
      font-family: $form-select-font-family;
      line-height: $form-select-line-height;
      background-color: $form-select-bg;
      border: $form-select-border-width solid $form-select-border-color;
      border-radius: $form-select-border-radius;
      box-shadow: $form-select-box-shadow;
      transition: $form-select-transition;

      &--multiple {
        &:after {
          position: absolute;
          top: 50%;
          right: $form-select-padding-x;
          display: block;
          width: nth(split-string(#{$form-select-bg-size}, " "), 1);
          height: nth(split-string(#{$form-select-bg-size}, " "), 2);
          background-image: escape-svg($form-select-indicator);
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          transform: translate(0, -50%);
          transition:
            transform 0.3s,
            background-image 0.3s;
          content: "";
        }

        &.select2-selection {
          padding-top: 0.4625rem;
          padding-bottom: 0.4625rem;
        }

        .select2-selection {
          &__choice {
            position: relative;
            display: inline-block;
            margin: 0.125rem 0.75rem 0.125rem 0;
            padding: 0.125rem 0.5rem 0.125rem 1.875rem;
            background-color: $primary;
            border-radius: $border-radius-xs;

            /* stylelint-disable selector-class-pattern */
            &__remove {
              position: absolute;
              top: 50%;
              left: 0.5rem;
              display: inline-flex;
              padding: 0;
              color: $gray-dark;
              font-size: 0.75rem;
              line-height: 1;
              background-color: transparent;
              border: none;
              transform: translateY(-50%);
              transition: color 0.3s;

              &:hover {
                color: $danger;
              }

              span {
                display: none;
              }

              &:after {
                display: inline-block;
                font-family: "icomoon";
                content: "\e911";
              }
            }

            /* stylelint-enable selector-class-pattern */
          }

          &__rendered {
            margin: 0;

            &:empty {
              display: none;

              + .select2-search {
                padding-top: 0.25rem;
                padding-bottom: 0.25rem;
              }
            }
          }
        }

        .select2-search {
          display: inline-block;
          margin: 0;

          &__field {
            @include placeholder {
              color: $input-placeholder-color;
              line-height: $input-line-height;
            }

            height: $input-font-size * $input-line-height;
            margin: 0;
            color: $input-color;
            font-weight: $input-font-weight;
            font-size: $input-font-size;
            font-family: inherit;
            line-height: $input-line-height;
            background-color: transparent;
            background-image: none;
            cursor: pointer;
            caret-color: $input-color;
          }
        }
      }

      &[aria-disabled="true"] {
        color: $form-select-disabled-color;
        background-color: $form-select-disabled-bg;
        border-color: $form-select-disabled-border-color;
        cursor: not-allowed;
      }

      &:not([aria-disabled="true"]) {
        &:hover {
          background-color: $yellow-light;
        }
      }

      &__placeholder {
        display: flex;
        height: calc($input-font-size * $input-line-height);
        color: $input-placeholder-color;
      }

      &__rendered {
        padding: 0 !important;
      }

      &__arrow {
        position: absolute;
        top: 50%;
        right: $form-select-padding-x;
        width: nth(split-string(#{$form-select-bg-size}, " "), 1);
        height: nth(split-string(#{$form-select-bg-size}, " "), 2);
        background-image: escape-svg($form-select-indicator);
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        transform: translate(0, -50%);
        transition:
          transform 0.3s,
          background-image 0.3s;
      }
    }

    .select2-dropdown {
      z-index: $zindex-dropdown;
      overflow: hidden;
      background-color: $form-select-bg;
      border: $form-select-border-width solid $form-select-focus-border-color;
      border-radius: $form-select-border-radius;

      &--above {
        transform: translateY(calc($form-select-border-width - 0.5rem));
      }

      &--below {
        transform: translateY(calc(-1 * var(--#{$prefix}border-width) + 0.5rem));
      }

      &.sm {
        .select2-results {
          &__option {
            padding: rem(8px);
          }
        }
      }
    }

    .select2-results {
      @extend %custom-scrollbar-sm;

      max-height: 210px;
      overflow: auto;

      &__options {
        &[aria-multiselectable="true"] {
          .select2-results__option {
            &:not(.select2-results__message) {
              position: relative;
              padding-left: 2.25rem;

              &:before {
                @include size(0.75rem);

                position: absolute;
                top: 0.95rem;
                left: 0.875rem;
                display: block;
                border: 1px solid $gray-dark;
                border-radius: 0.125rem;
                content: "";
              }

              &.select2-results__option--selected {
                background-color: transparent;

                &:before {
                  background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 8 7' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M7 1L2.8 6L1 4' stroke='#{$black}' stroke-linecap='round' stroke-linejoin='round'/></svg>"));
                  background-repeat: no-repeat;
                  background-position: center;
                  background-size: 0.5rem;
                  border-color: $dark;
                }

                &:hover {
                  background-color: $yellow-light;
                }
              }
            }
          }
        }
      }

      &__option {
        padding: $form-select-padding-y $form-select-padding-x;
        color: $form-select-color;
        font-weight: $form-select-font-weight;
        font-size: $form-select-font-size;
        font-family: $form-select-font-family;
        line-height: $form-select-line-height;
        transition:
          background-color 0.3s,
          color 0.3s;

        &:not(.select2-results__message, .select2-results__option--selected) {
          &:hover {
            background-color: $yellow-light;
          }
        }

        &.select2-results__option--selected {
          background-color: $primary;
        }
      }

      &__message {
        padding: 0.5rem;
      }
    }

    .select2-search {
      &.select2-search--dropdown {
        padding: 0;

        .select2-search__field {
          @include placeholder {
            color: $input-placeholder-color;
          }

          height: 34px;
          padding: 0.5rem $input-padding-x 0.5rem 2.25rem;
          color: $input-color;
          font-weight: $input-font-weight;
          font-size: $font-size-sm;
          font-family: $input-font-family;
          line-height: $input-line-height;
          background-color: $gray-light;
          background-image: escape-svg(
            url("data:image/svg+xml,<svg viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M14.3501 14.3562C14.2568 14.4481 14.1311 14.4998 14.0001 14.5C13.8673 14.4994 13.7398 14.448 13.6438 14.3562L10.9438 11.65C9.80671 12.6051 8.34474 13.0844 6.86285 12.9878C5.38095 12.8912 3.99355 12.2263 2.99 11.1316C1.98645 10.037 1.44424 8.59717 1.47645 7.11248C1.50867 5.62779 2.11282 4.21286 3.1629 3.16277C4.21298 2.11269 5.62791 1.50855 7.1126 1.47633C8.59729 1.44412 10.0371 1.98633 11.1317 2.98988C12.2264 3.99343 12.8913 5.38083 12.9879 6.86273C13.0845 8.34462 12.6052 9.80659 11.6501 10.9437L14.3501 13.6437C14.3973 13.6903 14.4349 13.7457 14.4605 13.8069C14.4861 13.868 14.4993 13.9337 14.4993 14C14.4993 14.0663 14.4861 14.1319 14.4605 14.1931C14.4349 14.2542 14.3973 14.3097 14.3501 14.3562ZM7.2501 12C8.18956 12 9.10792 11.7214 9.88906 11.1995C10.6702 10.6775 11.279 9.93567 11.6385 9.06772C11.998 8.19977 12.0921 7.24471 11.9088 6.3233C11.7255 5.40189 11.2732 4.55552 10.6089 3.89122C9.94455 3.22692 9.09819 2.77452 8.17678 2.59124C7.25537 2.40797 6.3003 2.50203 5.43235 2.86155C4.5644 3.22106 3.82255 3.82988 3.30062 4.61102C2.77868 5.39215 2.5001 6.31051 2.5001 7.24998C2.50175 8.50925 3.00273 9.71647 3.89317 10.6069C4.78361 11.4973 5.99083 11.9983 7.2501 12Z' fill='#{$input-placeholder-color}'/></svg>")
          );
          background-repeat: no-repeat;
          background-position: left $input-padding-x center;
          background-size: 16px;
          border: none;
          border-radius: 0;
          transition: $input-transition;
        }
      }
    }
  }
}

.form-select {
  &.form-select-sm {
    ~ .select2-container {
      &--bootstrap {
        .select2-selection {
          padding: $form-select-padding-y-sm rem(26px) $form-select-padding-y-sm $form-select-padding-x-sm;
          font-size: $form-select-font-size-sm;
          line-height: 1.14285714;
          border-radius: $form-select-border-radius-sm;

          &__arrow {
            @include size(rem(16px));

            right: $form-select-padding-x-sm;
            background-size: rem(12px);
          }
        }
      }
    }

    &.filter-select {
      ~ .select2-container {
        &--bootstrap {
          &.select2-container--open {
            .select2-selection {
              background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M7.52174 9.81818H11M7.52174 11.6364H10.1304M1 3H9.69565V4.81818L6.53391 8.90909V13L4.16174 12.0909V8.90909L1 4.81818V3Z' stroke='#{$form-select-focus-border-color}' stroke-width='0.8' stroke-linejoin='round'/></svg>"));
            }
          }

          .select2-selection {
            padding-left: rem(26px);
            background-image: escape-svg(url("data:image/svg+xml,<svg viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M7.52174 9.81818H11M7.52174 11.6364H10.1304M1 3H9.69565V4.81818L6.53391 8.90909V13L4.16174 12.0909V8.90909L1 4.81818V3Z' stroke='#{$form-select-indicator-color}' stroke-width='0.8' stroke-linejoin='round'/></svg>"));
            background-repeat: no-repeat;
            background-position: left $form-select-padding-x-sm center;
            background-size: rem(16px);
            border-color: $dark;
          }
        }
      }
    }
  }

  &.form-select-lg {
    ~ .select2-container {
      &--bootstrap {
        .select2-selection {
          padding: $form-select-padding-y-lg $form-select-indicator-padding $form-select-padding-y-lg $form-select-padding-x-lg;
          font-size: $form-select-font-size-lg;
          border-radius: $form-select-border-radius-lg;

          &__arrow {
            right: $form-select-padding-x-lg;
          }
        }

        .select2-dropdown {
          border-radius: $form-select-border-radius-lg;
        }

        .select2-results {
          &__option {
            padding: $form-select-padding-y-lg $form-select-padding-x-lg;
            font-size: $form-select-font-size-lg;
          }
        }
      }
    }
  }
}

.select2-hidden-accessible {
  @include visually-hidden;

  top: 0;
}
