.uploaded-files {
  padding: 12px;
  background-color: $gray-200;
  border-radius: $border-radius;

  @include media-breakpoint-up(sm) {
    padding: 16px;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  &__item-content {
    position: relative;
    display: flex;
    align-items: center;
    min-height: rem(42px);
    padding-left: rem(48px);
  }

  &__item-icon {
    position: absolute;
    top: 50%;
    left: 0;
    z-index: 5;
    width: rem(36px);
    height: rem(42px);
    transform: translate(0, -50%);
  }

  &__item-name {
    @include ellipsis;

    margin-bottom: 0;
    margin-bottom: rem(2px);
    color: $dark;
    font-weight: $font-weight-normal;
    font-size: $font-size-sm;
  }

  &__item-size {
    margin-bottom: 0;
    color: $gray-dark;
    font-weight: $font-weight-normal;
    font-size: rem(10px);
  }

  &__item-progress-block {
    position: relative;
    max-width: 432px;
    padding-top: rem(8px);
  }

  &__item-progress {
    position: relative;
    height: 3px;
    background-color: $gray-dark;
    border-radius: $border-radius-pill;
  }

  &__item-progress-bar {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 5;
    background-color: $primary;
    border-radius: $border-radius-pill;
    transition: width 0.3s;
  }

  &__item-progress-percentage {
    position: absolute;
    right: 0;
    bottom: 0;
    margin-bottom: 0;
    padding-bottom: rem(4px);
    color: $dark;
    font-weight: $font-weight-bold;
    font-size: rem(10px);
  }
}
